import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef } from '@angular/core';
import { IPublication } from '../../../@shared/interfaces/publication';
import { PUBLICATION_TYPE } from '../../../@shared/enums/publication-type';
import { TranslateService } from '@ngx-translate/core';
import { PUBLISHER_TYPE } from '../../../@shared/enums/publisher-type';
import { Constants } from 'src/app/@global/constants';
import { Subscription } from 'rxjs';
import { Store } from '@ngxs/store';
import { PostsState } from '../../../@store/posts/posts.state';
import { SetFocusOnPublication } from '../../../@store/posts/posts.actions';

export interface PublicationButtonClickResult {
  buttonPosition: number;
  publicationId: string;
}

export interface PublicationButton {
  outline: string;
  filled?: string;
}

const MAX_CHARACTERS = 70;
const MAX_TIME_VIDEO_TOUCH = 300;

@Component({
  selector: 'app-publication',
  templateUrl: './publication.component.html',
  styleUrls: ['./publication.component.scss'],
})
export class PublicationComponent implements OnInit {
  @ViewChild('gradientTop', { static: false }) gradientTop: ElementRef;
  @ViewChild('gradientBottom', { static: false }) gradientBottom: ElementRef;

  @Input() publication: IPublication = null;
  @Input() userPhoto: string;
  @Input() i: number;
  @Input() isDetail: boolean;
  @Input() buttonList: PublicationButton[];
  @Output() buttonClick = new EventEmitter<PublicationButtonClickResult>();
  @Output() showLikesList = new EventEmitter<string>();
  @Output() showCommentsList = new EventEmitter<string>();
  @Output() clickUserProfile = new EventEmitter<IPublication>();

  public publicationType = PUBLICATION_TYPE;
  public showLinkText: string;

  public like: PublicationButton = Constants.PUBLICATION_LIKE_BUTTON;

  public focusOnPublication = false;
  private focusOnPublicationSubscription: Subscription;
  private touchStartPositionY: number;
  public startVideoTouchTime: number;

  constructor(private translateService: TranslateService, private store: Store) {}

  ngOnInit() {
    this.showLinkText = this.translateService.instant('highlights.showMore');
    this.subscribeToFocusOnPublication();
  }

  private subscribeToFocusOnPublication(): void {
    this.focusOnPublicationSubscription = this.store.select(PostsState.isFocusOnPublication).subscribe((result) => {
      this.focusOnPublication = result;
    });
  }

  public needsCutText(description: string) {
    return description.length > MAX_CHARACTERS;
  }

  public toggleDescription() {
    let descriptionContainer: any;
    if (this.isDetail) {
      descriptionContainer = document
        .getElementById('detailPublication')
        .getElementsByClassName('publication-gradient-bottom')[0];
    } else {
      descriptionContainer = document.getElementsByClassName('publication-gradient-bottom')[this.i];
    }

    if (descriptionContainer.classList.contains('opened')) {
      this.showLinkText = this.translateService.instant('highlights.showMore');
      descriptionContainer.classList.remove('opened');
    } else {
      descriptionContainer.classList.add('opened');
      this.showLinkText = this.translateService.instant('highlights.showLess');
    }
  }

  public onButtonClick(index: number): void {
    this.buttonClick.emit({ buttonPosition: index, publicationId: this.publication.id });
  }

  public onShowLikesList(): void {
    this.showLikesList.emit(this.publication.id);
  }

  public onShowCommentsList(): void {
    this.showCommentsList.emit(this.publication.id);
  }

  public onClickUserProfile(): void {
    this.clickUserProfile.emit(this.publication);
  }

  public isUser() {
    return this.publication.publisher.type === PUBLISHER_TYPE.USER;
  }

  public toggleFocusOnPublication() {
    this.store.dispatch(new SetFocusOnPublication(!this.focusOnPublication));
  }

  public onVideoTouchStart(event: any) {
    this.startVideoTouchTime = Date.now();
    this.touchStartPositionY = event.changedTouches[0].clientY;
  }

  public onVideoTouchEnd(event: any) {
    const distanceY = Math.abs(event.changedTouches[0].clientY - this.touchStartPositionY);
    if (Date.now() - this.startVideoTouchTime <= MAX_TIME_VIDEO_TOUCH && distanceY <= 0) {
      this.toggleFocusOnPublication();
    }
  }

  private disconnectObservers(): void {
    if (this.focusOnPublicationSubscription) {
      this.focusOnPublicationSubscription.unsubscribe();
    }
  }

  ngOnDestroy(): void {
    this.disconnectObservers();
  }
}
